.groups {
  width: 120vw;
  display: flex;
  justify-content: center;
}

.center {
  display: flex;
  align-items: center;
}

.group {
  height: 125px;
  flex-direction: row;
}

.shadow {
  filter: drop-shadow(0 0 3px rgba(0,0,0,0.5));
}

.icon {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-size: auto 100px;
}
